import { AudioGraph, Outputs } from "@api/graph";
export class Track extends Outputs {
    constructor(name = "Track name") {
        super();
        this.enabled = true;
        this.height = 64;
        this.name = name;
    }
}
export class PluginTrack extends Track {
    constructor() {
        super(...arguments);
        this.plugin = "Plug-in";
        this.volume = 1.0;
        this.pan = 0.0;
    }
}
export class Project {
    constructor() {
        this.name = "New Project";
        this.tracks = [];
        this.graph = new AudioGraph();
    }
}
